@keyframes customEnterOverlayAnimation {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes customLeaveOverlayAnimation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes customEnterModalAnimation {
  0% {
    transform: scale(0.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes customLeaveModalAnimation {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.2);
  }
}

@keyframes show {
  0% {
    display: none;
    opacity: 0;
    transform: scale(0, 0);
  }
  1% {
    display: grid;
    opacity: 0;
  }
  100% {
    opacity: 1;
    transform: scale(1, 1);
  }
}

@keyframes nav {
  0% {
    height: 180%;
    width: 120%;
    border-radius: 50%;
    justify-content: space-around;
  }
  25% {
    height: 20%;
    width: 30%;
    border-radius: 50%;
    justify-content: space-around;
  }
  50% {
    height: 20%;
    width: 30%;
    border-radius: 50%;
    top: 0;
    justify-content: space-around;
  }
  75% {
    height: initial;
    width: 100%;
    border-radius: 0;
    top: 0;
    justify-content: space-between;
  }
  100% {
    justify-content: space-between;
  }
}
